<template>
    <div class="api">
        <h2>File API</h2>
        <div class="api-info">
            <h3>File API</h3>
            <p>JavaScript has always been good at working with strings and numbers, but binary data has never been its strong point.</p>
            <p>Recently, however, it added a blob data type and interfaces to work with blobs (binary large objects). JavaScript sees a blob as a big chunk of raw data.</p>
            <p>A blob is usually generated from a file, but not necessarily. It’s a good alternative to work with data without loading the entire file into memory, and it provides the possibility of processing binary information in small pieces.</p>
            <p>We use the &lt;input&gt; tag to select files.</p>
        </div>
        <pre class="api-code">
	&lt;input type="file" name="files" id="files" /&gt;
	</pre>
        <div class="api-info">
            <h3> Blob Interface (File API)</h3>
            <p>This interface provides properties and methods to operate with blobs. This interface is inherited by the File Interface.</p>
        </div>
        <h4 class="api-table-caption">The Methods</h4>
        <table class="api-table">
            <tr>
                <td>slice(start, length, type)</td>
                <td>This method returns the part of the blob or file indicated by the values in bytes of the start and length attributes.</td>
            </tr>
        </table>
        <h4 class="api-table-caption">The Properties</h4>
        <table class="api-table">
            <tr>
                <td>size</td>
                <td>This property represents the size of the blob or file in bytes.</td>
            </tr>
            <tr>
                <td>type</td>
                <td>This property represents the media type of the blob or file.</td>
            </tr>
        </table>
        <div class="api-info">
            <h3>File Interface (File API)</h3>
            <p>This interface is an extension of the Blob Interface to process files.</p>
        </div>
        <h4 class="api-table-caption">The Properties</h4>
        <table class="api-table">
            <tr>
                <td>name</td>
                <td>This property represents the name of the file.</td>
            </tr>
            <tr>
                <td>type</td>
                <td>This property represents the media type of the blob or file.</td>
            </tr>
        </table>
        <div class="api-info">
            <h3>FileReader Interface (File API)</h3>
            <p>This interface provides specific methods, properties and events for reading files and blobs into memory.</p>
        </div>
        <pre class="api-code">
	var fileReader=new FileReader(); 
	fileReader.readAsText(file, "UTF-16"); 
	fileReader.onprogress = updateProgress; 
	fileReader.addEventsListener('load', onLoad, false);
	</pre>
        <h4 class="api-table-caption">The Methods</h4>
        <table class="api-table">
            <tr>
                <td>readAsArrayBuffer(file)</td>
                <td>This method returns a file or blob’s content as an ArrayBuffer.</td>
            </tr>
            <tr>
                <td>readAsBinaryString(file)</td>
                <td>This method returns a file or blob’s content as a binary string.</td>
            </tr>
            <tr>
                <td>readAsText(file)</td>
                <td>This method interprets a file or blob’s content and returns it as a text.</td>
            </tr>
            <tr>
                <td>readAsDataURL(file)</td>
                <td>This method returns a file or blob’s content as a data:url.</td>
            </tr>
            <tr>
                <td>abort()</td>
                <td>This method aborts the reading process.</td>
            </tr>
        </table>
        <h4 class="api-table-caption">The Properties</h4>
        <table class="api-table">
            <tr>
                <td>result</td>
                <td>This property represents the data returned by the reading methods.</td>
            </tr>
        </table>
        <h4 class="api-table-caption">The Events</h4>
        <table class="api-table">
            <tr>
                <td>loadstart</td>
                <td>This event is fired when the read starts.</td>
            </tr>
            <tr>
                <td>progress</td>
                <td>This event is fired periodically to report reading status.</td>
            </tr>
            <tr>
                <td>load</td>
                <td>This event is fired when the reading is complete.</td>
            </tr>
            <tr>
                <td>abort</td>
                <td>This event is fired when the reading process is aborted.</td>
            </tr>
            <tr>
                <td>error</td>
                <td>This event is fired in case of failure.</td>
            </tr>
            <tr>
                <td>loadend</td>
                <td>This event is fired when the request is completed, either in success or failure.</td>
            </tr>
        </table>
        <div class="api-info">
            <h3>LocalFileSystem Interface (File API: Directories and System)</h3>
            <p>This interface is provided to initiate a File System for the application.</p>
        </div>
        <h4 class="api-table-caption">The Methods</h4>
        <table class="api-table">
            <tr>
                <td>requestFileSystem(type, size, success function, error function)</td>
                <td>This method requests the initialization of a File System configured according to the value of its attributes. The type attribute can take two values: TEMPORARY or PERSISTENT. The size must be specified in bytes.</td>
            </tr>
        </table>
        <div class="api-info">
            <h3>FileSystem Interface (File API: Directories and System)</h3>
            <p>This interface provides information about the File System.</p>
        </div>
        <h4 class="api-table-caption">The Properties</h4>
        <table class="api-table">
            <tr>
                <td>name</td>
                <td>This property represents the name of the File System.</td>
            </tr>
            <tr>
                <td>root</td>
                <td>This property references the root directory of the File System.</td>
            </tr>
        </table>
        <div class="api-info">
            <h3>Entry Interface (File API: Directories and System)</h3>
            <p>This interface provides methods and properties to process entries (files and directories) in the File System.</p>
        </div>
        <h4 class="api-table-caption">The Properties</h4>
        <table class="api-table">
            <tr>
                <td>isFile</td>
                <td>This property is a boolean value indicating whether the entry is a file or not.</td>
            </tr>
            <tr>
                <td>isDirectory</td>
                <td>This property is a boolean value indicating whether the entry is a directory or not.</td>
            </tr>
            <tr>
                <td>name</td>
                <td>This property represents the name of the entry.</td>
            </tr>
            <tr>
                <td>fullPath</td>
                <td>This property represents the full path from the root of the File System to the entry.</td>
            </tr>
            <tr>
                <td>filesystem</td>
                <td>This property contains a reference to the File System.</td>
            </tr>
        </table>
        <h4 class="api-table-caption">The Methods</h4>
        <table class="api-table">
            <tr>
                <td>moveTo(parent, new name, success function, error function)</td>
                <td>This method moves an entry to a different location. The parent attribute represents the directory in which the entry will be moved. The new name attribute, if specified, changes the name of the entry in the new location.</td>
            </tr>
            <tr>
                <td>copyTo(parent, new name, success function, error function)</td>
                <td>This method makes a copy of the entry. The parent attribute represents the directory in which the copy of the entry will be created. The new name attribute, if specified, changes the name of the copy.</td>
            </tr>
            <tr>
                <td>remove(success function, error function)</td>
                <td>This method deletes a file or an empty directory.</td>
            </tr>
            <tr>
                <td>getParent(success function, error function)</td>
                <td>This method returns the parent DirectoryEntry of the selected entry.</td>
            </tr>
        </table>
        <div class="api-info">
            <h3>DirectoryEntry Interface (File API: Directories and System)</h3>
            <p>This interface provides methods to create and read files and directories.</p>
        </div>
        <h4 class="api-table-caption">The Methods</h4>
        <table class="api-table">
            <tr>
                <td>createReader()</td>
                <td>This method creates a DirectoryReader object to read entries.</td>
            </tr>
            <tr>
                <td>getFile(path, options, success function, error function)</td>
                <td>This method creates or reads the file indicated by the path attribute. The options attribute is set by two flags: create and exclusive. The first one indicates whether the file will be created or not, and exclusive, when it is set to true, forces this method to return an error if the file already exists.</td>
            </tr>
            <tr>
                <td>getDirectory(path, options, success function, error function)</td>
                <td>This method creates or reads the directory indicated by the path attribute. The options attribute is set by two flags: create and exclusive. The first one indicates whether the directory will be created or not, and exclusive, when it is set to true, forces this method to return an error if the directory already exists</td>
            </tr>
            <tr>
                <td>removeRecursively(success function, error function)</td>
                <td>This method deletes a directory and all its content.</td>
            </tr>
        </table>
        <div class="api-info">
            <h3>DirectoryReader Interface (File API: Directories and System)</h3>
            <p>This interface provides the alternative to get a list of entries in a specific directory.</p>
        </div>
        <h4 class="api-table-caption">The Methods</h4>
        <table class="api-table">
            <tr>
                <td>readEntries(success function, error function)</td>
                <td>This method reads a block of entries from the selected directory. It returns null if no more entries are found.</td>
            </tr>
        </table>
        <div class="api-info">
            <h3>FileEntry Interface (File API: Directories and System)</h3>
            <p>This interface provides methods to get a File object from a specific file and a FileWriter object in order to add content to the file.</p>
        </div>
        <h4 class="api-table-caption">The Methods</h4>
        <table class="api-table">
            <tr>
                <td>createWriter(success function, error function)</td>
                <td>This method creates a FileReader object to write content into a file.</td>
            </tr>
            <tr>
                <td>file(success function, error function)</td>
                <td>This method returns a File object that represents the selected file.</td>
            </tr>
        </table>
        <div class="api-info">
            <h3>BlobBuilder Interface (File API: Writer)</h3>
            <p>This interface provides methods to work with blob objects.</p>
        </div>
        <h4 class="api-table-caption">The Methods</h4>
        <table class="api-table">
            <tr>
                <td>getBlob(type)</td>
                <td>This method returns the content of a blob object as a blob.</td>
            </tr>
            <tr>
                <td>append(data)</td>
                <td>This method appends data to a blob object. The interface provides three append() methods to append data as text, as a blob or as ArrayBuffer.</td>
            </tr>
        </table>
        <div class="api-info">
            <h3>FileWriter Interface (File API: Writer)</h3>
            <p>The FileWriter interface is an expansion of the FileSaver interface. The latter is not described here, but the events listed below are part of it.</p>
        </div>
        <h4 class="api-table-caption">The Properties</h4>
        <table class="api-table">
            <tr>
                <td>position</td>
                <td>This property represents the current position in which the next writing will occur.</td>
            </tr>
            <tr>
                <td>length</td>
                <td>This property represents the length of the file in bytes.</td>
            </tr>
        </table>
        <h4 class="api-table-caption">The Methods</h4>
        <table class="api-table">
            <tr>
                <td>write(blob)</td>
                <td>This method writes content in a file.</td>
            </tr>
            <tr>
                <td>seek(offset)</td>
                <td>This method sets a new position in which the next writing will occur.</td>
            </tr>
            <tr>
                <td>truncate(size)</td>
                <td>This method changes the length of the file to the value of the size attribute (in bytes).</td>
            </tr>
        </table>
        <h4 class="api-table-caption">The Events</h4>
        <table class="api-table">
            <tr>
                <td>writestart</td>
                <td>This event is fired when the write starts.</td>
            </tr>
            <tr>
                <td>progress</td>
                <td>This event is fired periodically to report writing status.</td>
            </tr>
            <tr>
                <td>write</td>
                <td>This event is fired when the writing is complete.</td>
            </tr>
            <tr>
                <td>abort</td>
                <td>this event is fired when the writing process is aborted.</td>
            </tr>
            <tr>
                <td>error</td>
                <td>This event is fired in case of failure.</td>
            </tr>
            <tr>
                <td>writeend</td>
                <td>This event is fired when the request is completed, either in success or failure.</td>
            </tr>
        </table>
        <div class="api-info">
            <h3>FileError Interface (File API and extensions)</h3>
            <p>Several methods in this API return a value through a callback function when the process fails. This value can be compared with the following list to find the corresponding error:</p>
        </div>
        <table class="api-table">
            <tr>
                <td>NOT_FOUND_ERR—value 1</td>
            </tr>
            <tr>
                <td>SECURITY_ERR—value 2</td>
            </tr>
            <tr>
                <td>ABORT_ERR—value 3</td>
            </tr>
            <tr>
                <td>NOT_READABLE_ERR—value 4</td>
            </tr>
            <tr>
                <td>ENCODING_ERR—value 5</td>
            </tr>
            <tr>
                <td>NO_MODIFICATION_ALLOWED_ERR—value 6 </td>
            </tr>
            <tr>
                <td>INVALID_STATE_ERR—value 7</td>
            </tr>
            <tr>
                <td>SYNTAX_ERR—value 8</td>
            </tr>
            <tr>
                <td>INVALID_MODIFICATION_ERR—value 9</td>
            </tr>
            <tr>
                <td>QUOTA_EXCEEDED_ERR—value 10</td>
            </tr>
            <tr>
                <td>TYPE_MISMATCH_ERR—value 11</td>
            </tr>
            <tr>
                <td>PATH_EXISTS_ERR—value 12</td>
            </tr>
        </table>
    </div>
</template>
<script>
export default {
    name: 'file',
    data() {
        return {}
    }
}
</script>



